import * as React from 'react';
import * as ReactDom from 'react-dom';
import { Version } from '@microsoft/sp-core-library';
import {
  BaseClientSideWebPart,
  IPropertyPaneConfiguration,
  PropertyPaneTextField
} from '@microsoft/sp-webpart-base';

import * as strings from 'zionStrings';
import Zion from './components/Zion';
import { IZionProps } from './components/IZionProps';
import { IZionWebPartProps } from './IZionWebPartProps';

export default class ZionWebPart extends BaseClientSideWebPart<IZionWebPartProps> {

  public render(): void {
    const element: React.ReactElement<IZionProps > = React.createElement(
      Zion,
      {
        title: this.properties.title,        
        description: this.properties.description,
        url: this.properties.url
      }
    );

    //var coolstuff: React.ReactElement<IZionProps> = React.createElement(Zion, ) = "<div><h1>Cool stuff</h1></div>"

    ReactDom.render(element, this.domElement);
  }

  protected get dataVersion(): Version {
    return Version.parse('1.0');
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField('title', {
                  label: 'Title'
                }),
                PropertyPaneTextField('description', {
                  label: 'Description',
                  multiline: true
                }),
                PropertyPaneTextField('url', {
                  label: 'URL'
                })
              ]
            }
          ]
        }
      ]
    };
  }
}
